body {
  background: #030017;
  color: #ffffff;
  padding: 0;
  margin: 0;
  font-family: "Poppins", "san-serif";
}

ul {
  padding: 0;
  margin: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
}

.ghost {
  border: 1px solid rgba(255, 255, 255, 0.64);
  box-sizing: border-box;
  border-radius: 24px;
  padding: 8px 16px;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.before-title {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.16em;
  color: #b394f4;
  text-align: center;
  display: block;
}

header {
  margin-top: 24px;
}
header .container .row .col-md-6 {
  display: flex;
  align-items: center;
}
header .container .row .col-md-6 ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .container .row .col-md-6 ul li {
  padding: 0 1rem;
}
header .container .row .col-md-6 {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .container .row .col-md-3:last-child {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

main div .container {
  margin: 150px 0;
}
main div .container h2 {
  margin-bottom: 64px;
  display: block;
  text-align: center;
}
main .section .container .glide .glide__track .glide__slides .glide__slide {
  border: 1px solid black;
  line-height: 100px;
  margin: 0;
  text-align: center;
}
main .section .container .glide .slider-controls {
  display: flex;
  justify-content: flex-end;
  margin: -94px 0 0 0;
  z-index: 1;
  position: absolute;
  right: 0;
}
main .section .container .glide .slider-controls .slider-arrow {
  width: 48px;
  height: 48px;
  font-size: 32px;
  border-radius: 50%;
  border: none;
  margin: 0 2px;
  background: #181346;
  color: white;
  font-weight: 200;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.94);
}
main .section .container .glide .slider-controls .slider-arrow:nth-child(2) {
  padding: 0 1px 2px 2px;
}
main .section .container .glide .slider-controls .slider-arrow:nth-child(1) {
  padding: 0 2px 2px 1px;
  background: none;
  color: rgba(255, 255, 255, 0.56);
}
main .section .container .glide .slider-controls .slider-arrow:nth-child(1):hover {
  background: #181346;
  color: rgba(255, 255, 255, 0.94);
}
main .section-opening .container {
  padding: 0 0 150px;
}
main .section-opening .container h1 {
  font-weight: 600;
  font-size: 86px;
  line-height: 135%;
}
main .section-opening .container h1, main .section-opening .container p, main .section-opening .container span, main .section-opening .container a {
  text-align: center;
}
main .section-opening .container span {
  display: block;
  font-size: 20px;
}
main .section-opening .container span a {
  display: inline-block;
  background: blue;
  padding: 16px 32px;
  margin: 32px auto;
  background: linear-gradient(256.86deg, #0CF1D7 -37.8%, #114EF7 58.71%, #A91BFF 102.4%, #FC7541 123.84%), #FFFFFF;
  border-radius: 48px;
}
main .section-about .container {
  padding: 0 0 64px;
}
main .section-about .container .row {
  align-items: center;
}
main .section-about .container .row div .glide {
  width: 418px;
  height: 418px;
}
main .section-about .container .row div .glide .glide__track {
  overflow: hidden;
  border-radius: 16px;
}
main .section-about .container .row div .glide .glide__track li.glide__slide .card {
  width: 418px;
  height: 418px;
  left: 137.25px;
  border-radius: 16px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
main .section-about .container .row div .glide .glide__track li.glide__slide #card-about-1 {
  background-color: #2B277C;
  background-image: url("https://images.unsplash.com/photo-1573497620053-ea5300f94f21?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8N3x8aW50ZXJ2aWV3fGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60");
}
main .section-about .container .row div .glide .glide__track li.glide__slide #card-about-2 {
  background-color: #2B277C;
  background-image: url("https://images.unsplash.com/photo-1532619187608-e5375cab36aa?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=500&q=80");
}
main .section-about .container .row div .glide .glide__track li.glide__slide #card-about-3 {
  background-color: #2B277C;
  background-image: url("https://images.unsplash.com/photo-1545235617-9465d2a55698?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=500&q=80");
}
main .section-about .container .row div .glide .glide__bullets {
  bottom: -32px;
}
main .section-about .container .row div .glide:before {
  content: "";
  background: #15125C;
  width: 160px;
  height: 160px;
  border-radius: 32px;
  display: block;
  position: absolute;
  top: -26px;
  left: -30px;
}
main .section-about .container .row div .before-title {
  text-align: left;
}
main .section-about .container .row div h2 {
  font-weight: 500;
  font-size: 48px;
  line-height: 135%;
  text-align: left;
  margin: 24px 0;
}
main .section-about .container .row div p {
  font-weight: 300;
  font-size: 18px;
  line-height: 150%;
}
main .section-about .container .row div span.steps {
  font-weight: 400;
  font-size: 21px;
  line-height: 150%;
  margin-top: 1rem;
  display: block;
}
main .section-about .container .row div span.steps span {
  color: rgba(255, 255, 255, 0.5);
}
main .section-how-work .container .row {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
main .section-how-work .container .row .icon-item {
  padding: 5px;
  width: 18%;
  margin: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
main .section-how-work .container .row .icon-item img {
  width: 36px;
}
main .section-how-work .container .row .icon-item h2 {
  text-align: center;
  font-size: 16px;
  font-weight: 500;
}
main .section-works .container .card-work {
  padding: 64px 86px;
  background: #181346;
  border-radius: 32px;
  min-height: 100px;
  margin: 100px 0;
  align-items: center;
}
main .section-works .container .card-work div {
  position: relative;
}
main .section-works .container .card-work div span {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #b394f4;
  display: block;
  margin-bottom: 10px;
  width: 100%;
}
main .section-works .container .card-work div h3 {
  margin: 20px 0 10px;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
  width: 100%;
}
main .section-works .container .card-work div h3 span {
  font-size: 14px;
  line-height: 30px;
  color: #FFFFFF;
  display: inline;
}
main .section-works .container .card-work div p {
  font-weight: 400;
  font-size: 16px;
  line-height: 170%;
  color: rgba(255, 255, 255, 0.72);
  width: 100%;
  margin-bottom: 0;
}
main .section-works .container .card-work div img {
  height: 400px;
  width: auto;
  position: absolute;
  top: -200px;
  margin: 0 15%;
}
main .section-works .container .card-work:nth-child(4) div img {
  margin: 0 0 0 -24px;
}
main .section-shots .container .row div img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}
main .section-shots .container .flex-center {
  margin: 48px auto;
}
main .section-clients .container .row div {
  display: flex;
  justify-content: center;
  align-items: center;
}
main .section-clients .container .row div img {
  max-width: 100%;
  max-height: 100%;
}
main .section-testimonials .container .row {
  align-items: center;
}
main .section-testimonials .container .row div img {
  border-radius: 24px;
  width: 85%;
  height: auto;
}
main .section-testimonials .container .row div .icon-quote {
  display: block;
  width: 36px;
  height: 36px;
  background: transparent url("assets/img/quote.svg") no-repeat center center;
}
main .section-testimonials .container .row div p {
  text-align: left;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: rgba(255, 255, 255, 0.72);
}
main .section-testimonials .container .row div .testimony-identity {
  padding-top: 16px;
  padding-bottom: 48px;
}
main .section-testimonials .container .row div .testimony-identity span {
  text-align: left;
  display: block;
}
main .section-testimonials .container .row div .testimony-identity .testimony-name {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  color: #FFFFFF;
  margin-bottom: 8px;
}
main .section-testimonials .container .row div .testimony-identity .testimony-role {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.5);
}
main .section-action .container {
  padding: 100px;
  background: #181346;
  border-radius: 24px;
}
main .section-action .container h2 {
  font-weight: 600;
  font-size: 48px;
  line-height: 72px;
  margin-top: 0;
  margin-bottom: 40px;
}
main .section-action .container span {
  display: block;
  font-size: 20px;
  text-align: center;
}
main .section-action .container span a {
  text-align: center;
  display: inline-block;
  background: blue;
  padding: 16px 32px;
  margin: 0px auto;
  background: linear-gradient(256.86deg, #0CF1D7 -37.8%, #114EF7 58.71%, #A91BFF 102.4%, #FC7541 123.84%), #FFFFFF;
  border-radius: 48px;
}

footer .container .row {
  padding: 56px 0;
}
footer .container .row > div {
  display: flex;
  align-items: center;
}
footer .container .row .col-md-5 ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
footer .container .row .col-md-5 ul li {
  padding: 0 1rem;
}
footer .container .row .col-md-5 ul li:first-child {
  padding: 0 0;
}
footer .container .row .col-md-2:nth-child(2) {
  justify-content: center;
}
footer .container .row .col-md-5:last-child {
  justify-content: flex-end;
}
footer .container .row .col-md-5:last-child ul li a span {
  width: 24px;
  height: 24px;
  display: inline-block;
  margin-right: 10px;
}
footer .container .row .col-md-5:last-child ul li ul li a #icon-dribbble {
  background-image: url("https://hyperfantasy.co/assets/styles/images/icon-social-media-dribbble.png");
}
footer .container .row .col-md-5:last-child ul li ul li a #icon-instagram {
  background-image: url("https://hyperfantasy.co/assets/styles/images/icon-social-media-instagram.png");
}
footer .container .row .col-md-5:last-child ul li ul li:first-child {
  padding: 0 0;
  margin-right: 10px;
}
footer .container .row .col-md-5:last-child ul li:first-child {
  font-size: 12px;
  margin-right: 28px;
}

@media (max-width: 768px) {
  header .container .row .col-md-6 {
    display: none;
  }
  header .container .row .col-md-3:last-child {
    display: none;
  }

  main .section-opening .container h1 {
    font-size: 36px;
  }

  footer .container .row {
    padding: 56px 0 0;
  }
  footer .container .row > div {
    display: flex;
    align-items: center;
  }
  footer .container .row .col-md-5 {
    justify-content: center;
  }
  footer .container .row .col-md-5 ul {
    flex-direction: column;
    padding: 1.25em 0;
  }
  footer .container .row .col-md-5 ul li {
    padding: 0.35em 0;
  }
  footer .container .row .col-md-5 ul ul {
    flex-direction: row;
  }
  footer .container .row .col-md-5:first-child {
    order: 2;
  }
  footer .container .row .col-md-2:nth-child(2) {
    order: 1;
  }
  footer .container .row .col-md-5:last-child {
    order: 3;
    justify-content: center;
  }
  footer .container .row .col-md-5:last-child ul li:first-child {
    margin: 0;
  }
  footer .container .row .col-md-5:last-child ul li a span {
    display: block;
    margin: 0 auto;
  }
  footer .container .row .col-md-5:last-child ul ul {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  footer .container .row .col-md-5:last-child ul ul li:first-child {
    padding: 1em;
    margin: 0;
  }
  footer .container .row .col-md-5:last-child ul ul li {
    padding: 1em;
    margin: 0;
  }
}